.POPOSList {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2em;
  max-width: 1200px;
  margin: auto;
  padding: 2em;
}

.POPOSList-Container form {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 20%;
  margin: auto;
}

.POPOSList-Container form input, .POPOSList-Container form button {
  padding: 1em;
  margin: 0;
  border: 1px solid;
  font-size: 1rem;
}

.POPOSList-Container form input {
  flex: 1;
  border-top-left-radius: 0.5em;
  border-bottom-left-radius: 0.5em;
}

.POPOSList-Container form button {
  border-top-right-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
}

@media only screen and (max-width: 960px) {
  .POPOSList {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 700px) {
  .POPOSList {
    grid-template-columns: 1fr;
  }
}
